import BScroll from 'better-scroll'
import axios from 'axios'
import tools from '@/common/tools.js'
import { mapState } from 'vuex'

export default {
    name: 'confirmorder',
    data: function() {
      return {
        canshupopShow: false,
        allData: {},
        yhqData: [],
        yhqTxt: '',
        yhqPrice: 0,
        chooseIndex: 'reset',
        nowYhqId: '',
        goodsFee: 0, // 总价
        payables: 0 // 使用优惠券后的价钱
      }
    },
    created() {
      /* this.setBuyNowStatus()
      this._getOrderData() */
    },
    computed: mapState([
      'addressData'
    ]),
    activated() {
      this.setBuyNowStatus()
      this._getOrderData()
    },
    methods: {
      editAddress() {
        this.$router.push({path: 'shipaddress', query: {type: 'confirmorder'}})
      },
      gotoAddress() {
        this.$router.push({path: 'addaddress', query: {type: 'confirmorder'}})
      },
      setBuyNowStatus() {
        this.canshupopShow = false
      },
      submitOrder() {
        // coupon_id,address_id
        let url = '/Mobile/Cart/cart3.html'
        axios.post(url, {
          'coupon_id': this.nowYhqId,
          'address_id': this.addressData.address_id,
          'action': this.$route.query.action,
          'act': 'submit_order',
          'goods_id': this.$route.query.goods_id,
          'goods_num': this.$route.query.goods_num,
          'item_id': this.$route.query.item_id
        })
          .then((res) => {
            console.log(res)
            if (res.data.status === 1) {
              // this.allData.splice(this.delNum, 1)
              // tools.showError(res.data.msg)
              let order_id = res.data.result.order_id
              this.$router.push({'path': 'submitorder', query: { order_id: order_id}})
            }
          })
          .catch((error) => {
            console.log(error)
          })
      },
      closedPop() {
        if (this.chooseIndex === 'reset') {
          this.canshupopShow = false
        } else {
          let url = '/Mobile/Cart/cart3.html'
          axios.post(url, {
            'coupon_id': this.nowYhqId,
            'action': this.$route.query.action,
            'goods_id': this.$route.query.goods_id,
            'goods_num': this.$route.query.goods_num,
            'item_id': this.$route.query.item_id
          })
            .then((res) => {
              console.log(res)
              if (res.data.status == 1) {
                // this.allData.splice(this.delNum, 1)
                this.goodsFee = res.data.result.goodsFee
                this.payables = res.data.result.payables
                this.yhqPrice = res.data.result.couponFee
                this.yhqTxt = this.yhqData[this.chooseIndex].coupon.name
                this.canshupopShow = false
                // tools.showError(res.data.msg)
              } else {
                tools.showError(res.data.msg)
              }
            })
            .catch((error) => {
              console.log(error)
            })
        }
      },
      changeYhqIndex(index) {
        if (this.chooseIndex === index) {
          this.chooseIndex = 'reset'
        } else {
          this.chooseIndex = index
          this.nowYhqId = this.yhqData[this.chooseIndex].id
        }
        console.log(this.chooseIndex)
      },
      _getOrderData() {
        let url = '/Mobile/Cart/cart2.html'
        axios.post(url, {
          goods_id: this.$route.query.goods_id,
          goods_num: this.$route.query.goods_num,
          item_id: this.$route.query.item_id,
          action: this.$route.query.action
        })
          .then((res) => {
            console.log(res)
            if (res.data.status === 1) {
              this.allData = res.data
              this.yhqData = res.data.userCartCouponList
              this.goodsFee = this.payables = this.allData.cartPriceInfo.total_fee
              console.log(this.$store.state.addressData)
              if (Object.keys(this.$store.state.addressData).length !== 0) {

              } else {
                this.$store.commit('SET_ADDRESSDATA', res.data.address)
                console.log(this.$store.state)
                // this.$store.commit('LOADING_SHOW', true)
              }
              if (this.yhqData.length < 1) {
                this.yhqTxt = '无可用的优惠券'
              } else {
                this.yhqTxt = this.yhqData.length + '张可用'
              }
            // tools.showError(res.data.msg)
            } else {
              tools.showError(res.data.msg)
              this.$router.push({path: 'shopcart'})
            }
          })
          .catch((error) => {
            console.log(error)
          })
      },
      openYhqPop() {
        if (this.yhqData.length > 0) {
          this.canshupopShow = true
          setTimeout(() => {
            this._initGuiGeScroll()
          }, 20)
        }
      },
      _initGuiGeScroll() {
        if (!this.YhqScroll) {
          this.YhqScroll = new BScroll(this.$refs.cspopList, {
            scrollY: true,
            startY: 0
          })
        }
      }
    },
    components: {

    }
}



